import React from "react";
import styles from "./CustomCard.module.css";
function CustomButton(props) {
    return (
        <React.Fragment>
            <button
                type="button"
                className={[
                    "btn btn-outline-primary",
                    styles.customButton,
                ].join(" ")}
                onClick={props.clickHandle}
            >
                {props.buttonName}
            </button>
        </React.Fragment>
    );
}

export default CustomButton;
