import React, { useState } from "react";
import { Container, Row, Col, Alert } from "react-bootstrap";
import CustomButton from "./CustomButton";
import styles from "./CustomCard.module.css";

const Counter = () => {
    var [counter, CounterFunction] = useState(0);
    var [AlertObj, AlertFunction] = useState({
        AlertMessage: "",
        show: false,
    });

    var incrementCounter = () => {
        if (AlertObj.show) {
            AlertFunction({ ...AlertObj, show: false });
        }
        CounterFunction((counter) => counter + 1);
    };

    var decrementCounter = () => {
        counter === 0
            ? AlertFunction({
                  AlertMessage: "Counter cannot be negative !",
                  show: true,
              })
            : CounterFunction((counter) => counter - 1);
    };

    var resetCounter = () => {
        if (AlertObj.show) {
            AlertFunction({ ...AlertObj, show: false });
        }

        CounterFunction((counter = 0));
    };

    return (
        <Container>
            <Row className="justify-content-center">
                <Col className="mt-3" xl={8}>
                    <div
                        className={[
                            "card bg-light border-warning",
                            styles.myCard,
                        ].join(" ")}
                    >
                        <div
                            className={[
                                "card-header bg-dark text-white text-center",
                                styles.cardHeader,
                            ].join(" ")}
                        >
                            <b className="d-block">Counter </b>
                            <h1 className="text-warning">{counter}</h1>
                        </div>
                        <div className="card-body">
                            <Alert
                                show={AlertObj.show}
                                variant="danger"
                                onClose={() =>
                                    AlertFunction({ ...AlertObj, show: false })
                                }
                                dismissible
                            >
                                <b>{AlertObj.AlertMessage} </b>
                            </Alert>
                            <Row className="text-center">
                                <Col className="col-12 col-md-4">
                                    <CustomButton
                                        clickHandle={() => incrementCounter()}
                                        buttonName="Increment Counter"
                                    />
                                </Col>

                                <Col className="pt-3 pt-md-0 col-12 col-md-4">
                                    <CustomButton
                                        clickHandle={() => decrementCounter()}
                                        buttonName="Decrement Counter"
                                    />
                                </Col>

                                <Col className="pt-3 pt-md-0 col-12 col-md-3">
                                    <CustomButton
                                        clickHandle={() => resetCounter()}
                                        buttonName="Reset"
                                    />
                                </Col>
                            </Row>
                        </div>
                    </div>
                </Col>
            </Row>
        </Container>
    );
};

export default Counter;
