import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Sidebar from "./Sidebar";
import styles from "./CustomCard.module.css";

const WindowResize = (props) => {
    const [windowWidth, setWindowWidth] = useState(window.innerWidth);

    useEffect(() => {
        function captureWindowResizeEvent() {
            const currentWidth = window.innerWidth;
            setWindowWidth(currentWidth);
        }
        window.addEventListener("resize", captureWindowResizeEvent);
        return () => {
            window.removeEventListener("resize", captureWindowResizeEvent);
        };
    }, []);
    return (
        <React.Fragment>
            <Device
                windowWidth={windowWidth}
                DeviceMetaData={props.DeviceMetaData}
            />
        </React.Fragment>
    );
};

const Device = (props) => {
    const device = props.DeviceMetaData.filter(
        (device) =>
            device.deviceMaxWidth >= props.windowWidth &&
            device.deviceMinWidth <= props.windowWidth
    );
    return (
        <Container>
            <Row className="justify-content-center">
                <Col className="mt-3">
                    <div
                        className={[
                            "card bg-light border-warning",
                            styles.myCard,
                        ].join(" ")}
                    >
                        <div
                            className={[
                                "card-header bg-dark text-white text-center",
                                styles.cardHeader,
                            ].join(" ")}
                        >
                            <b className="d-block">Home</b>
                        </div>
                        <div className="card-body">
                            <Row className="justify-content-center">
                                <Col className="justify-content-center">
                                    <div className="text-center">
                                        <b>Window Size: </b>
                                        <b className="text-warning text-center">
                                            {props.windowWidth}
                                        </b>
                                    </div>

                                    <div className="text-center">
                                        <img
                                            className="img-fluid"
                                            src={require(`../Assets/${device[0].deviceImageURL}`)}
                                            alt={
                                                device[0].deviceType + " Device"
                                            }
                                        />
                                        <h3 className="text-center">
                                            {device[0].deviceType}
                                        </h3>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                    </div>
                </Col>
                {device[0].deviceMinWidth > 1025 ? <Sidebar /> : null}
            </Row>
        </Container>
    );
};

export default WindowResize;
